/***/
@font-face {
  font-family: "Grifter";
  src: url("../fonts/grifterbold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  list-style: none;
}

body {
  font-family: "Proxima Nova", serif;
  font-size: 50px;
  background: #140E1B;
  color: #fff;
  position: relative;
  min-height: 100vh;
  padding-bottom: 120px;
}

button {
  background: transparent;
  font-family: inherit;
  outline: none;
  border: none;
}

.container {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
}
@media (max-width: 1440px) {
  .container {
    max-width: unset;
    padding-inline: 60px;
  }
}
@media (max-width: 768px) {
  .container {
    padding-inline: 0;
    max-width: 92%;
  }
}

.ellipse {
  z-index: -1;
  position: absolute;
  width: 588px;
  height: 588px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  filter: blur(254px);
  background: rgb(108, 37, 190);
  opacity: 0.3;
}
@media (max-width: 768px) {
  .ellipse {
    width: 350px;
    height: 350px;
    filter: blur(150px);
  }
}

.hidden {
  display: none;
}

.pages {
  width: 175px;
  height: 21px;
  color: rgba(255, 255, 255, 0.4);
  font-family: GRIFTER;
  font-size: 16px;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: 0%;
  text-align: left;
  right: 100px;
  bottom: 70px;
  position: absolute;
}
@media (max-width: 768px) {
  .pages {
    right: 20px;
  }
}

.pages__white {
  color: #fff;
}

.pages__arrowLeft {
  margin-right: 27px;
  transform: translateY(2px);
  opacity: 0.4;
}
.pages__arrowLeft--active {
  opacity: 1;
}

.pages__arrowRight {
  margin-left: 27px;
  transform: translateY(2px);
  opacity: 0.4;
}
.pages__arrowRight--active {
  opacity: 1;
}

.header {
  padding-top: 53px;
  height: 60px;
  position: relative;
  z-index: 1;
}

.header-wrapper {
  display: flex;
  align-items: center;
}

.header__leftPart {
  display: flex;
  align-items: center;
}

.main-menu__item {
  font-size: 18px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.7);
  margin-right: 60px;
}
.main-menu__item:last-child {
  margin-right: 0;
}
.main-menu__item--active {
  color: rgb(255, 255, 255);
  position: relative;
}
.main-menu__item--active::after {
  content: "";
  position: absolute;
  background: rgb(108, 37, 190);
  height: 8px;
  width: 8px;
  left: 50%;
  transform: translateX(-50%);
  bottom: -12px;
  border-radius: 50%;
}
@media (max-width: 1440px) {
  .main-menu__item {
    margin-right: 30px;
  }
}
@media (max-width: 1168px) {
  .main-menu__item {
    display: none;
  }
}

.menu-logo {
  color: rgb(255, 255, 255);
  font-family: GRIFTER;
  font-size: 34px;
  font-weight: 700;
  line-height: 130%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 1168px) {
  .menu-logo {
    position: static;
    transform: translateX(0);
  }
}

.header__rightPart {
  margin-left: auto;
  display: flex;
}

.mobileBtn {
  display: none;
}
@media (max-width: 1168px) {
  .mobileBtn {
    display: block;
  }
}

.mobileMenu {
  position: relative;
  z-index: 2;
  background: #000;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  min-height: 100px;
  padding: 20px;
  font-size: 24px;
  font-weight: 700;
  line-height: 250%;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  padding-block: 50px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.mobileMenu__item--active {
  color: rgb(255, 255, 255);
  position: relative;
}

.header__close {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 26px;
  height: 26px;
}

.brand-block {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.brand-block--enter .brand {
  font-size: 150px;
}
@media (max-width: 768px) {
  .brand-block--enter .brand {
    font-size: 20vw;
  }
}

.brand {
  animation: pulsing alternate ease-in-out 2s infinite;
  color: rgb(108, 37, 190);
  font-family: GRIFTER;
  font-size: 176px;
  font-weight: 700;
  line-height: 130%;
  text-align: center;
  text-shadow: rgb(99, 21, 189) 0 0 64px;
}
@media (max-width: 768px) {
  .brand {
    font-size: 25vw;
  }
}

.brand__slogan {
  color: rgb(255, 255, 255);
  font-family: GRIFTER;
  font-size: 24px;
  font-weight: 700;
  line-height: 150%;
  text-align: center;
}
@media (max-width: 768px) {
  .brand__slogan {
    font-size: 20px;
  }
}

.brand__text {
  margin-top: 6px;
  color: rgb(255, 255, 255);
  font-family: GRIFTER;
  font-size: 18px;
  font-weight: 700;
  line-height: 150%;
  text-align: center;
}
@media (max-width: 768px) {
  .brand__text {
    font-size: 16px;
  }
}

@keyframes pulsing {
  0% {
    text-shadow: rgb(99, 21, 189) 0 0 6px;
  }
  100% {
    text-shadow: rgb(99, 21, 189) 0 0 64px;
  }
}
.features__title {
  color: rgb(255, 255, 255);
  font-family: GRIFTER;
  font-size: 40px;
  font-weight: 700;
  line-height: 150%;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 20px;
}

.features__wrapper {
  display: flex;
  gap: 56px;
  width: 100%;
}
@media (max-width: 1168px) {
  .features__wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.features__left {
  max-width: 632px;
  display: flex;
  flex-direction: column;
  gap: 38px;
  width: 100%;
}

.features__right {
  max-width: 632px;
  margin-top: 104px;
  display: flex;
  flex-direction: column;
  gap: 38px;
  width: 100%;
}
@media (max-width: 1168px) {
  .features__right {
    margin-top: 0;
  }
}

.features-card {
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.2));
  width: 100%;
  min-height: 231px;
  border-radius: 15px;
  box-shadow: 0px 4px 50px 0px rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(15px);
  background: linear-gradient(226.15deg, rgba(60, 66, 75, 0.03) 0.225%, rgba(57, 65, 77, 0.69) 101.52%);
  padding: 35px 40px;
  display: flex;
  gap: 33px;
}

.features-card__img {
  width: 56px;
  height: 56px;
}

.features-card__title {
  font-family: GRIFTER;
  font-size: 20px;
  font-weight: 700;
  line-height: 130%;
  max-width: 230px;
  margin-bottom: 12px;
}
.features-card__title--long {
  max-width: 272px;
}

.features-card__text {
  color: rgb(255, 255, 255);
  font-family: Proxima Nova;
  font-size: 18px;
  font-weight: 400;
  line-height: 130%;
  text-align: left;
}

.multichain__title {
  color: rgb(255, 255, 255);
  font-family: GRIFTER;
  font-size: 40px;
  font-weight: 700;
  line-height: 150%;
  text-align: center;
  margin-top: 211px;
  margin-bottom: 67px;
}
@media (max-width: 1440px) {
  .multichain__title {
    margin-top: 150px;
  }
}
@media (max-width: 768px) {
  .multichain__title {
    margin-top: 100px;
  }
}

.multichain__wrapper {
  display: flex;
  gap: 27px;
  width: 100%;
  justify-content: center;
}
@media (max-width: 1440px) {
  .multichain__wrapper {
    flex-wrap: wrap;
  }
}

.multichain-card {
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.2));
  width: 100%;
  min-height: 310px;
  border-radius: 15px;
  box-shadow: 0px 4px 50px 0px rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(15px);
  background: linear-gradient(226.15deg, rgba(60, 66, 75, 0.03) 0.225%, rgba(57, 65, 77, 0.69) 101.52%);
  padding: 45px;
  max-width: 422px;
}

.multichain-card__title {
  font-family: GRIFTER;
  font-size: 20px;
  font-weight: 700;
  line-height: 130%;
  max-width: 230px;
  margin-bottom: 5px;
}

.multichain-card__text {
  color: rgb(255, 255, 255);
  font-family: Proxima Nova;
  font-size: 18px;
  font-weight: 400;
  line-height: 130%;
  text-align: left;
}

.revenue__title {
  color: rgb(255, 255, 255);
  font-family: GRIFTER;
  font-size: 40px;
  font-weight: 700;
  line-height: 150%;
  text-align: center;
  margin-top: 211px;
  margin-bottom: 67px;
}
@media (max-width: 1440px) {
  .revenue__title {
    margin-top: 150px;
  }
}
@media (max-width: 768px) {
  .revenue__title {
    margin-top: 100px;
  }
}

.revenue__wrapper {
  display: flex;
  gap: 24px;
  width: 100%;
  justify-content: center;
}
@media (max-width: 1440px) {
  .revenue__wrapper {
    flex-wrap: wrap;
  }
}

.revenue-card {
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.2));
  width: 100%;
  min-height: 344px;
  border-radius: 15px;
  box-shadow: 0px 4px 50px 0px rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(15px);
  background: linear-gradient(226.15deg, rgba(60, 66, 75, 0.03) 0.225%, rgba(57, 65, 77, 0.69) 101.52%);
  padding: 45px;
  max-width: 648px;
}

.revenue-card__title {
  font-family: GRIFTER;
  font-size: 20px;
  font-weight: 700;
  line-height: 130%;
  max-width: 230px;
  margin-bottom: 18px;
  position: relative;
  padding-left: 50px;
}
.revenue-card__title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background: url("../img/check.svg");
  width: 30px;
  height: 20px;
}

.revenue-card__text {
  color: rgb(255, 255, 255);
  font-family: Proxima Nova;
  font-size: 18px;
  font-weight: 400;
  line-height: 130%;
  text-align: left;
}
